.top-bar {
  padding-block: 25px 0;
  display: flex; /* елементи розташув горизонтально */
  justify-content: space-between; /* розсунути елементи */
  align-items: center; /* центування елементів по лінії */

  @include on-tablet {
    padding-block: 72px 0;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 46px;

    @include hover(transform, scale(1.2));

    @include on-tablet {
      height: 65px;
    }
  }
}
