.about-us {
  @include page-grid;

  &__title {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 3;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__content {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 4 / 7;
    }

    @include on-desktop {
      grid-column: span 7;
    }

    &--title {
      margin: 0;
      margin-bottom: 16px;
      padding: 0;
      font-size: 24px;
      font-weight: 700;
      line-height: 30px;
      text-align: left;
    }
  }

  &__schedule-on {
    display: flex;
    gap: 24px;
  }

  &__schedule-time {
    display: grid;
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    list-style: none;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
  }

  &__schedule-day {
    display: grid;
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    list-style: none;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
  }

  &__schedule-off {
    &--title {
      margin: 0;
      font-size: 16px;
      font-weight: 700;
      line-height: 27px;
    }

    &--text {
      margin: 0;
      margin-bottom: 24px;
      padding: 0;
      font-size: 16px;
      font-weight: 400;
      line-height: 27px;
    }
  }
}
