.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  text-align: center;
  letter-spacing: -1.5px;

  @include on-tablet {
    margin-bottom: 48px;
    font-size: 48px;
  }

  @include on-desktop {
    font-size: 48px;
  }

  &--align--left {
    text-align: left;
    margin-bottom: 32px;
    font-size: 32px;
  }

  &--plan-visit {
    color: #d12d35;
    font-size: 12px;
    font-weight: 700;
    line-height: 16.8px;
    text-align: left;
    margin: 0;
    margin-bottom: 2px;
  }

  &--subtitle-on {
    color: #d12d35;
    text-align: center;
    margin: 0;
    margin-bottom: 4px;
  }

  &--text {
    margin: 0;
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
  }

  &--text--n-o-view {
    margin-bottom: 48px;
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
  }

  &--n-o-view {
    margin-bottom: 8px;
  }

  &--more-to-explore {
    margin-bottom: 40px;
  }
}
