@import './blocks/utils/variables';
@import './blocks/utils/mixins';
@import 'blocks/header';
@import 'blocks/page';
@import 'blocks/top-bar';
@import 'blocks/icon';
@import 'blocks/menu';
@import 'blocks/nav';
@import 'blocks/section-title';
@import 'blocks/show';
@import 'blocks/about-us';
@import 'blocks/now-on-view';
@import 'blocks/explore-online';
@import 'blocks/contacts';
