.main {
  --main-color-button: #d12d35;
  --main-color-button-hover: #ae1c23;
  --main-text-color: #fff;

  background-color: #fff;

  &__content {
    display: grid;
    row-gap: 100px;
    padding-block: 100px;

    @include on-tablet {
      padding-block: 140px;
      row-gap: 140px;
    }
  }
}
