.icon {
  display: block;
  background-size: cover;
  background-position: center;
  height: 24px;
  width: 24px;
  transition: transform 0.3s;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  &__phone {

    &--image{
      background-image: url(../icons/icon-phone.svg);
      margin-left: 16px;
    }

    &--contacts-info{
      display: none;
      position: absolute;
      top: -40px;
      left: -190px;
      text-align: right;
      z-index: 100;
    }

    &-menu {
      position: relative;

      &:hover .icon__phone--contacts-info {
        display: none;

        @include on-tablet {
          display: none;
        }

        @include on-desktop {
          display: block;
        }
      }

      .icon__phone--contacts-info:focus-within::before  {
        display: block;
      }
    }

    &--info {
      font-size: 16px;
      font-weight: 700;
      line-height: 27px;
      text-decoration: none;
      color: $main-text-color;
      text-align: right;
    }
  }

  &--menu {
    background-image: url(../icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../icons/icon-close.svg);
  }
}
