.header {
  box-sizing: border-box; /* щоб хедер займав 100 відсотків і не бильше від висоти екранa */
  height: 100vh; /* щоб розтягнувся хедер навсю ширину екрану */
  background-size: cover; /* щоб фон покривав усю прлощу і не задвоювало картинку */
  background-position: center; /* відцентровуємо зображення */

  @media all and (max-width: 575px) {
    &__image-header {
      background-size: cover;
      background-position: center;
      background-image: url(../images/header/header-bg-mobile.png);
    }
  }

  @media all and (min-width: 576px) and (max-width: 1019px) {
    &__image-header{
      background-size: cover;
      background-position: center;
      background-image: url('../images/header/header-bg-tablet.png');
    }
  }

  @media all and (min-width: 1020px) {
      &__image-header {
        background-size: cover;
        background-position: center;
        background-image: url('../images/header/header-bg-dt-2x.png');
      }
  }

  &__content {
    color: $main-text-color;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    font-family: Cinzel, serif;
    font-weight: 700;
    line-height: 100%;
    text-align: center;
    color: $main-text-color;
    font-size: 40px;

    @include on-tablet {
      font-size: 64px;
    }

    @include on-desktop  {
      font-size: 80px;
    }
  }

  &__bottom {
    padding-top: 129px;
    padding-bottom: 288px;

    @include on-tablet {
      padding-top: 141px;
      padding-bottom: 456px;
    }

    @include on-desktop  {
      padding-top: 83px;
      padding-bottom: 482px;
    }
  }
}
