.contacts {
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
}

.contact {
  &__address{
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 3;
    }

    @include on-desktop {
      grid-column: 1 / 7;
    }
  }

  &__address-the-met {
    margin: 0;
    margin-bottom: 16px;
  }

  &--name {
    font-size: 14px;
    font-weight: 400;
    margin: 0;
    margin-bottom: 4px;
  }

  &__number-phone {
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 27px;
    letter-spacing: 0;
    color: inherit;
    text-decoration: none;
    margin: 0;
    margin-bottom: 8px;

    transition: transform 0.3s ease;

    &:hover {
      text-decoration: underline;
    }
  }

  &--text-bold {
    margin: 0;
    text-decoration: none;
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
    text-align: left;
    color: inherit;

    transition: transform 0.3s ease;

    &:hover {
      text-decoration: underline;
    }
  }

  @include page-grid;

  @media all and (min-width: 1020px) {
    &__address{
      display: flex;
      gap: 24px;
      border: 0;
    }
  }

  &__fifth-avenue {
    grid-template-columns: 1fr;

    @include on-tablet {
      grid-template-columns: 1fr 1fr;
    }

    @include on-desktop {
      grid-template-columns: 1fr 1fr 1fr;
    }

    margin-bottom: 48px;
  }

  &__cloisters {
    grid-template-columns: 1fr;

   @include on-tablet {
      grid-template-columns: 1fr 1fr;
    }

    @include on-desktop {
      grid-template-columns: 1fr 1fr 1fr;
    }

    margin-bottom: 48px;
  }

  &__form {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 4 / -1;
    }

    @include on-desktop {
      grid-column: 8 / -1;
    }

    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    textarea:-webkit-autofill,
    textarea:-webkit-autofill:hover,
    textarea:-webkit-autofill:focus,
    select:-webkit-autofill,
    select:-webkit-autofill:hover,
    select:-webkit-autofill:focus {
      font-family: Cinzel, serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;

      -webkit-text-fill-color: #161616;
      transition: background-color 50000s ease-in-out 0s;
    }

    &--small{
      width:100%;
      height: 48px;
      gap: 0;
      border: 0;
      font-family: Cinzel, serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      text-align: left;
      text-indent: 10.9px;
      color: #7e7e83;
      text-transform: uppercase;
      background-color: #f8f8fa;
      margin: 0;
      margin-bottom: 16px;
      outline: none; /* Видаляє контур фокусу */

      &:not(:placeholder-shown) {
        color: #161616;
      }

      &:focus {
        border: 1px solid #161616;
      }
    }

    &--big {
      height: 144px;
      resize: none; /* Вимикаємо можливість зміни розміру */

      width:100%;
      gap: 0;
      border: 0;
      font-family: Cinzel, serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      text-align: left;
      text-indent: 10.9px;
      color: #7e7e83;
      text-transform: uppercase;
      background-color: #f8f8fa;
      margin: 0;
      outline: none; /* Видаляє контур фокусу */
      margin-bottom: 32px;

      &:not(:placeholder-shown) {
        color: #161616;
      }

      &:focus {
        border: 1px solid #161616;
      }
    }

  }

  &__button-send {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    gap: 0;
    border: 0;
    width:100%;
    height: 40px;
    cursor: pointer;
    background-color: var(--main-color-button);
    font-family: Cinzel, serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    text-align: center;
    color: var(--main-text-color);
    text-decoration: none;
    text-transform: uppercase;
  }

  &__button-send:hover {
    color: var(--main-text-color);
    background-color: var(--main-color-button-hover);
  }

  &__button-send:active {
    color: var(--main-text-color);
    background-color: #EB343D;
  }
}


