.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #d12d35;
  color: $main-text-color;
  font-weight: 700; /* жирність літер тексту */
  text-transform: uppercase; /* щоб літери стали великими */
  overflow: auto;

  &__top {
    position: sticky; /* to keep cross always visible */
    top: 0;
    z-index: 1;
    margin-bottom: 40px;

    @include on-tablet {
      margin-bottom: 40px;
    }

    @include on-desktop {
      margin-bottom: 55px;
    }
  }

  &__nav {
    margin-bottom: 32px;
  }

  &__the-met-fifth-avenue {
    display: block;
    font-size: 16px;
    line-height: 27px;
    letter-spacing: 0;
    color: inherit;
    text-decoration: none;
  }

  &__phone-namber-1 {
    display: block;
    margin-bottom: 8px;
    font-size: 16px;
    line-height: 27px;
    letter-spacing: 0;
    color: inherit;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &__the-met-cloisters {
    display: block;
    font-size: 16px;
    line-height: 27px;
    letter-spacing: 0;
    color: inherit;
    text-decoration: none;
  }

  &__phone-namber-2 {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    letter-spacing: 0;
    color: inherit;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &__call-to-order {
    display: block;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;
    width: fit-content; /* ширина не буде змін, а просто не буде розтягуватись до макс розм батьківськ елем */
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
