.explore-online {
  &__shows {
    @include page-grid;

    row-gap: 48px;

    transition: transform 0.3s ease; /* Smooth transition effect */

    :hover img {
      transform: scale(1.1); /* Enlarge the image on hover */
    }
  }

  &__show {
    overflow: hidden;/* Заборона виходу за межі контейнера */

    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }
}
