.page {
  font-family: Cinzel, serif;
  color: #161616;
  font-size: 16px;
  line-height: 140%;
  scroll-behavior: smooth; /* плавна прокрутка */

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0; /* прозорість */
    transition: all 0.3s;
    transform: translateX(-100px);
    pointer-events: none; /* щоб не заважало взаємодіяти з елементами */

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden; /* not to scroll the page */
}
