.now-on-view {
  &__wrapper {
    display: grid;
    row-gap: 48px;
  }
}

.view {
  &__photos {
    @include page-grid;

    margin-bottom: 24px;

    &--three-square{
      grid-template-columns: repeat(3,  1fr);

      @include on-tablet {
        grid-template-columns: repeat(3, 1fr 1fr);
      }

      @include on-desktop {
        grid-template-columns: repeat(4,  1fr 1fr 1fr);
      }
    }

    transition: transform 0.3s ease; /* Smooth transition effect */

    :hover img {
      transform: scale(1.1); /* Enlarge the image on hover */
    }
  }

  &__link {
    overflow: hidden; /* Заборона виходу за межі контейнера */
    position: relative;

    &--wide {
      @include on-tablet {
        grid-column: span 4;
      }

      @include on-desktop {
        grid-column: span 8;
      }
    }

    &--square {
      @include on-tablet {
        grid-column: span 2;
      }

      @include on-desktop {
        grid-column: span 4;
      }
    }

    @media all and (min-width: 576px) and (max-width: 744px) {
      &--square-tablet{
        @include on-tablet {
          grid-column: span 3;
        }
      }
    }
  }

  &__photo {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 100% 100%;

    &--with-texst {
      object-position: 25% 100%;
    }

    @media all and (min-width: 576px) and (max-width: 744px) {
      &--three-invisible {
        display: none;
      }

      &--visible {
        width: 288px;
        height: 284px;
        gap: 24px;
      }
    }
  }

  &__event {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    text-align: left;
    color: #d12d35;
  }

  &__date-event {
    margin: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    text-align: left;
    color: #161616;
  }

  &__button {
    @include page-grid;

    row-gap: 40px;
    margin-top: 40px;
  }

  &__button-view {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / 6;
    }

    @include on-desktop {
      grid-column: 5 / 9;
    }

    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    height: 40px;
    border: 1px solid var(--main-color-button);
    text-decoration: none;
    text-transform: uppercase;
    background: var(--main-color-button);
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    text-align: center;
    color: var(--main-text-color);
  }

  &__button-view:hover {
    color: var(--main-text-color);
    background: var(--main-color-button-hover);
  }

  &__button-view:active {
    color: var(--main-text-color);
    background-color: #EB343D;
  }
}
