.footer {
  margin: 0;
  bottom: 0;
  width: 100%;
  z-index: -1 fixed;

  @media all and (max-width: 575px) {
    &__image {
      height: 180px;
      width: 100%;
      margin: 0;
      background-size: cover;
      background-position: center;
      background-image: url('../images/footer/image-footer-mob.png');
    }
  }

  @media all and (min-width: 576px) and (max-width: 1019px) {
    &__image {
      height: 359px;
      width: 100%;
      margin: 0;
      background-size: cover;
      background-position: center;
      background-image: url('../images/footer/image-footer-tabl.png');
    }
  }

  @media all and (min-width: 1020px) {
      &__image {
        height: 359px;
        width: 100%;
        margin: 0;
        background-size: cover;
        background-position: center;
        background-image: url('../images/footer/image-footer-dt.png');
      }
  }
}

